.list {
    color: white;
    width: 350px;
    background-color: #34495e;
    min-height: 10px;
    margin: 20px auto;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 0px;
}

.list p, span {
    padding-left: 10px;
    padding-right: 10px;
}

span{
    cursor: pointer;
}

