.App {
  background-color: #2d3436;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  padding-bottom: 10px;
}

body{
  background-color: #636e72;
}

#to-do-form {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0px;
}

#to-do-form input, input:focus {
  background-color: #b2bec3;
  border: none;
  width: 300px;
  padding: 11px;
  outline: none;
  margin: 20px;
  color: white;
  font-weight: bold;
  margin-right: 8px;
  border-radius: 2px;
}

#to-do-form input::placeholder{ 
  color: white;
  font-weight: 100;
}

#to-do-form button, button:focus {
  height: 37px;
  width: 70px;
  background-color: #0097e6;
  border: none;
  color: white;
  font-weight: bold;
  margin-right: 20px;
  border-radius: 2px;
  cursor: pointer;
  outline: none;
}